.Head {
    margin-top: 15px;
    margin-bottom: 10px;
  }
  
  .CardWrapper {
    width: auto;
    height: auto;
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
    background: #f1f1f1;
    cursor: pointer;
  }
  
  .OrderHead,
  .OrderFooter {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  @media screen and (max-width: 425px) {
    .CardWrapper {
      width: 100%;
    }
  }